import React from "react";
import { FiChevronRight, FiChevronLeft } from "react-icons/fi";
import { FaQuoteRight } from "react-icons/fa";
import { FaStar } from "react-icons/fa";
import { FiStar } from "react-icons/fi";
import data from "./data";
function App() {
  const [people, setPeople] = React.useState(data);
  const [index, setIndex] = React.useState(0);
  React.useEffect(() => {
    const end = people.length - 1;
    if (index < 0) {
      setIndex(end);
    }
    if (index > end) {
      setIndex(0);
    }
  }, [index, people]);
  React.useEffect(() => {
    let t = setInterval(() => {
      setIndex(index + 1);
    }, 3000);
    return () => {
      clearInterval(t);
    };
  }, [index]);
  return (
    <section className="section">
      <div className="title">
        <h2>
          <span>/</span>
          Reviews
        </h2>
      </div>
      <div className="section-center">
        {people.map((person, i) => {
          const { id, image, name, title, star, quote } = person;
          const stars = Array(5).fill(false).fill(true, 0, star);
          console.log(stars);
          let pos = "nextSlide";
          if (index === i) {
            pos = "activeSlide";
          }
          if (index === i - 1 || (i === 0 && index === people.length - 1)) {
            pos = "lastSlide";
          }
          return (
            <article className={pos} key={id}>
              <img src={image} alt={name} className="person-img" />
              <h4>{name}</h4>
              <p className="title">{title}</p>
              <p className="star">
                {stars.map((s, i) => {
                  return s ? (
                    <FaStar key={name + "-" + i} />
                  ) : (
                    <FiStar key={name + "-" + i} />
                  );
                })}
              </p>
              <p className="text">{quote}</p>
              <FaQuoteRight className="icon" />
            </article>
          );
        })}
        <button className="prev" onClick={() => setIndex(index - 1)}>
          <FiChevronLeft />
        </button>
        <button className="next" onClick={() => setIndex(index + 1)}>
          <FiChevronRight />
        </button>
      </div>
    </section>
  );
}

export default App;
